import React from 'react';
import './Article.css';
import TextImg from './TextImg'
import pic6 from "./img/pic06.jpg";
import pic7 from "./img/pic07.jpg";
import pic8 from "./img/pic08.jpg";



function Article() {
    return (
        <article>
                <div className='imgText'>
                    <img src = {pic6} alt="pic6"/> 
                    <TextImg classText='rightDiv' headline='Viverra Hendrerit'/>
                </div>
                <div className='imgText'>
                    <TextImg classText='leftDiv' headline='Curabitur Eget'/>
                    <img src = {pic7} alt="pic7"/>
                </div>
                <div className='imgText'>
                    <img src = {pic8} alt="pic8"/>
                    <TextImg classText='rightDiv' headline='Morbi Eleifend'/>
                </div>
        </article>
    )
}

export default Article
